<template>
    <ul class="photo-list">
        <router-link tag="li" :to="{name:'photoinfo',params:{id:item.id}}" v-for="item in list" :key="item.id">
            <img v-lazy="item.img_url">
            <div class="info">
                <h1 class="info-title">
                    {{item.title}}
                </h1>
                <div class="info-body">{{item.zhaiyao}}</div>
            </div>
        </router-link>
    </ul>
</template>
<script>
import { Lazyload } from 'mint-ui';

export default {
    props:[
        'list'
    ],
    data() {
        return {
        }
    }
}
</script>

<style lang="scss" scoped>
	.photo-list {
        list-style:none;
        margin:0;
        padding:10px;
        padding-bottom:0px;
        li {
            text-align:center;
            margin-bottom:10px;
            background:#ccc;
            box-shadow:0 0 10px #999;
            position:relative;
            img{
                width:100%;
                vertical-align:middle;
            }
            img[lazy=loading] {
                width: 40px;
                height: 300px;
                margin: auto;
            }
            .info{
                color:#fff;
                text-align:left;
                position:absolute;
                bottom:0px;
                background:rgba(0,0,0,0.5);
                max-height:90px;
                padding:5px;
                box-sizing:border-box;
                .info-title{
                    font-size:14px;
                }
                .info-body{
                    font-size:13px;
                }
            }
        }
    }
</style>


