<template>
    <div>
        <!-- 顶部滑动条 -->
        <top-nav v-on:bindTab="handleClick"/>
        <!-- 图片列表区域 -->
        <photo-list :list='list' />
    </div>
</template>
<script>
import TopNav from '@/components/photolist/topNav'
import PhotoList from '@/components/photolist/photolist'
import api from '@/api/index.js'
import { Toast } from 'mint-ui'

export default {
    data() {
        return {
            list:[],
            cateId:0
        }
    },
    created() {
        this.getPhotoListByCateId(this.cateId);
    },
    methods: {
        getPhotoListByCateId(id){
            api.getPhotoListByCateId(id).then(res => {
                if(res.data.status === 0) {
                    if(res.data.message.length == 0) {
                        Toast('暂无数据');
                    }
                    this.list = res.data.message;
                } else {
                    Toast('请求数据失败')
                }
            })
        },
        handleClick(params) {
            this.getPhotoListByCateId(params.cateId);
        }
    },
    components:{
        TopNav,
        PhotoList
    }
}
</script>
<style lang="scss" scoped>
    
</style>


